.App{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 800px; 
  box-sizing:border-box;
  padding: 50px}
  .button{
    display: block;
    margin: 0 auto;
    margin-top: 30px;
    background-color: mediumaquamarine;
    border: none;
    padding: 15px 20px;
    font-size: 25px;
    text-transform: uppercase;
    border-radius: 4px;
  transition: all 200ms ease-in-out;
  
  }
  .button-primary{
    background-color: orangered;
  }
  .button:hover,
  .button:focus{
    background-color:coral;
    cursor: pointer;
  }
  .wrapper{
    border: 1px solid whitesmoke;
    border-radius: 4px;
    width: 100%;
  height: 90%;
  display: flex;
  
  }
  
  .button-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .djiga{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
    .foto{
  
      width:50%;
      height:100%;
    }
    .sofia{
      display: block;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
    .godfather{
      background: url("./right-feet.jpg"), url("./left-feet.jpg");
      background-position: right bottom 10%, left  top 40%;
      background-repeat: no-repeat, no-repeat;
      background-size: 200px , 200px ;
      width: 50%;
      padding:  50px 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      
    }
    .title{
      text-align: center;
      font-family: 'Dancing Script', cursive;
      font-size: 60px;
      font-weight: 300;
    }
  
  p{ 
  
    padding:0;
    text-align: center;
  margin: 0;
    font-size: 30px;
    text-transform: uppercase;
  }
  
  
